import React, { Component } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { deleteBooking } from "../../Actions/projectActions";

class BookingItem extends Component {
  onDeleteClick = id => {
    this.props.deleteBooking(id);
  };

  render() {
    const { booking } = this.props;

    return (
      <div className="ui secondary piled raised segment">
        <div className="ui link cards centered floated">
          <div className="card-width ui raised card">
            <div className="content">
              <h2 className="ui center aligned icon header">
                Booking details{" "}
              </h2>
            </div>

            <div className="content">
              <div className="header">Personal info</div>

              <h3 className="ui sub header medium left floated">
                Parent / Guardian's Name:{" "}
              </h3>
              <h3 className="ui sub header large left floated">
                {booking.parentFirstName} {booking.parentLastName}
              </h3>
              <br></br>
              <h3 className="ui sub header medium left floated">
                Kid's Name:{" "}
              </h3>
              <h3 className="ui sub header large left floated">
                {booking.attendeeFirstName} {booking.attendeeLastName}{" "}
              </h3>
              <br></br>
              <h3 className="ui sub header large floated left">
                Kid's Age: {booking.age}{" "}
              </h3>
              <br></br>
              <h3 className="ui sub header medium left floated">
                Attending Date:{" "}
              </h3>
              <h3 className="ui sub header large left floated">
                {booking.bookingDate}{" "}
              </h3>
              <h5 className="ui sub header medium">
                ActivityID: {booking.activityIdentifier}
              </h5>
            </div>

            <div className="content">
              <div className="header">Contact info</div>
              <h5 className="ui sub header medium left floated">Email: </h5>
              <h3 className="ui sub header large left floated">
                {booking.emailAddress}{" "}
              </h3>
              <br></br>
              <h5 className="ui sub header medium left floated">Phone: </h5>
              <h3 className="ui sub header large left floated">
                {booking.phoneNumber}
              </h3>
              <br></br>
            </div>

            <div className="content">
              <div className="header">Additional info:</div>
              <h4 className="ui sub header">{booking.additionalInfo}</h4>
            </div>

            <div className="extra content">
              {/* As a design decision we are currently not implementing updates for a booking
                Only new bookings are set, keeping this as a placeholder for future development
                 <Link
                to={`/updateBooking/${booking.id}`}
                class="ui button positive medium "
              >
                Edit booking info
              </Link>
           */}
              <div
                class="ui button negative medium "
                onClick={this.onDeleteClick.bind(this, booking.id)}
              >
                Delete booking
              </div>
              <br></br>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

BookingItem.propTypes = {
  deleteBooking: PropTypes.func.isRequired
};

export default connect(
  null,
  { deleteBooking }
)(BookingItem);
