import React, { Component } from "react";
import imageTest from "../../../images/spykidrope.jpg";
import LazyLoad from "react-lazy-load";
class Double00 extends Component {
  render() {
    return (
      <React.Fragment>
        <div class="ui program-bg vertical segment holder">
          <div class="ui container">
            <div class="ui stackable inverted divided equal height stackable grid">
              <div class="six wide column img-place">
                <img src={imageTest} className="animate-image" />
              </div>
              <div class="ten wide column">
                <h2 class="ui center aligned icon header">
                  <i class="circular users icon"></i>
                  Double 00 Spy School
                </h2>
                <div class="ui divider"></div>
                <div class="change-font">
                  Children want to run free, create, investigate and explore. We
                  offer all this and so much more! Our facility includes an
                  open-space parkour gym, to help elevate natural reflexes and
                  master the craft of climbing, free running and an overall
                  active lifestyle! And after you're done your parkour training,
                  put your physical abilities to the test and see if you have
                  what it takes to be an undercover spy. At Double 'OO'
                  International Super Spy School, we need YOU to help us keep
                  Belleville villain free!  Don't know about parkour? That's
                  okay! Learn about the art of free-running and our other
                  programs by clicking the links below.
                </div>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Double00;
