import React from "react";
import { Link } from "react-router-dom";

const ActivityButton = () => {
  return (
    <React.Fragment>
      <div className="admin-buttons">
        <Link
          to="/AdminPortal/ViewAllActivities"
          className="ui blue submit button"
        >
          View Activities
        </Link>
      </div>
    </React.Fragment>
  );
};

export default ActivityButton;
