import React, { Component } from "react";
import { connect } from "react-redux";
import { getAdminBookings } from "../../Actions/projectActions";
import PropTypes from "prop-types";
import BookingItem from "../BookProgram/BookingItem";

class AdminViewBooking extends Component {
  componentDidMount() {
    this.props.getAdminBookings();
  }

  render() {
    const bookings = this.props.booking.bookings;

    return (
      <React.Fragment>
        {bookings.map(booking => (
          <BookingItem key={booking.id} booking={booking} />
        ))}
      </React.Fragment>
    );
  }
}

AdminViewBooking.propTypes = {
  booking: PropTypes.object.isRequired,
  getAdminBookings: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  booking: state.booking
});

export default connect(
  mapStateToProps,
  { getAdminBookings }
)(AdminViewBooking);
