import React, { Component } from "react";
import CloseButton from "./CloseButton";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import classnames from "classnames";
import { login } from "../../Actions/secActions";

class Login extends Component {
  constructor() {
    super();
    this.state = {
      email: "",
      password: "",
      errors: {}
    };

    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.security.validToken) {
      this.props.history.push("/home");
    }

    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onSubmit(e) {
    //prevents form refresh
    e.preventDefault();
    const LoginRequest = {
      email: this.state.email,
      password: this.state.password
    };

    this.props.login(LoginRequest);
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }
  render() {
    const { errors } = this.state;

    return (
      <div id="login-main">
        <div className="login-center">
          <div className="ui middle aligned center aligned grid">
            <div className="column-login">
              <form className="ui large form" onSubmit={this.onSubmit}>
                <div className="login-transparent ui stacked segment ">
                  <CloseButton />
                  <div
                    className={classnames("field login-padd-top", {
                      "is-invalid": errors.email
                    })}
                  >
                    <div className="ui left icon input">
                      <i className="user icon"></i>
                      <input
                        className="input-bg"
                        type="text"
                        name="email"
                        autoComplete="off"
                        placeholder="E-mail address"
                        value={this.state.email}
                        onChange={this.onChange}
                      />
                    </div>
                    {errors.email && (
                      <div className="login-error-control">{errors.email}</div>
                    )}
                  </div>
                  <div
                    className={classnames("field login-padd-top", {
                      "is-invalid": errors.password
                    })}
                  >
                    <div className="ui left icon input">
                      <i className="lock icon"></i>
                      <input
                        className="input-bg"
                        type="password"
                        name="password"
                        placeholder="Password"
                        value={this.state.password}
                        onChange={this.onChange}
                      />
                    </div>

                    {errors.password && (
                      <div className="login-error-control">
                        {errors.password}
                      </div>
                    )}
                  </div>
                  <button
                    className="submit-spce ui fluid large red submit button"
                    type="submit"
                  >
                    Login
                  </button>
                </div>
              </form>

              <div className="help-links-login login-transparent ui message">
                <a className="align-to-right" href="#">
                  Forgot Password?
                </a>
                <a href="/Register">Register Here</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Login.propTypes = {
  login: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired,
  security: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  security: state.security,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { login }
)(Login);
