import React from "react";

import "./App.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Header from "./components/Layout/Header/Header";
import ImageText from "./components/Layout/ImageText/ImageText";
import QParkour from "./components/Layout/QParkour/QParkour";
import Programs from "./components/Programs/Programs";
import { Provider } from "react-redux";
import store from "./store";
import Double00 from "./components/Layout/Double00/Double00";
import Footer from "./components/Layout/Footer/Footer";
import AboutUs from "./components/AboutUs/AboutUs";
import BookProgramForm from "./components/BookProgram/BookProgramForm";
import Login from "./components/LoginRegister/Login";
import { withRouter } from "react-router";
import Register from "./components/LoginRegister/Register";
import Waiver from "./components/BookProgram/WaiverForm";
import DisplayBookings from "./components/BookProgram/DisplayBookings";
import UpdateBooking from "./components/BookProgram/UpdateBooking";

//Admin
import AdminHome from "./components/AdminPortal/AdminHome";
import AdminViewProgram from "./components/AdminPortal/ViewProgram";
import ViewAllAvtivities from "./components/AdminPortal/ViewAllActivities";
import InsertActivityForm from "./components/AdminPortal/InsertActivityForm";
import UpdateActivityForm from "./components/AdminPortal/UpdateActivityForm";
import DeleteActivityPage from "./components/AdminPortal/DeleteActivityPage";
import AdminViewBooking from "./components/AdminPortal/AdminViewBooking";

//Security
import jwt_decode from "jwt-decode";
import setJWToken from "./Utilities/setJWToken";
import { SET_CURRENT_USER } from "./Actions/ActionType";
import { logout } from "./Actions/secActions";
import SecuredRoute from "./Utilities/SecureRoute";
//import AddChildren from "./components/BookProgram/AddChildren";
import Client from "shopify-buy";
import Shop from "./components/shopify/Main";
import ContactUs from "./components/AboutUs/ContactUs";

const jwtToken = localStorage.jwtToken;

//set JW Token again b/c when pages change or refresh occurs, token disappears
if (jwtToken) {
  setJWToken(jwtToken);
  const decode_jwtToken = jwt_decode(jwtToken);
  store.dispatch({
    type: SET_CURRENT_USER,
    payload: decode_jwtToken
  });

  const currentTime = Date.now() / 1000;

  if (decode_jwtToken.exp < currentTime) {
    //handle logout
    store.dispatch(logout());
    window.location.href = "/";
  }
}

const client = Client.buildClient({
  storefrontAccessToken: "9643df77cf8f4f2330b2f19bed06e3be",
  domain: "the-station-belleville.myshopify.com"
});
store.dispatch({ type: "CLIENT_CREATED", payload: client });
// buildClient() is synchronous, so we can call all these after!
client.product.fetchAll().then(res => {
  store.dispatch({ type: "PRODUCTS_FOUND", payload: res });
});
client.checkout.create().then(res => {
  store.dispatch({ type: "CHECKOUT_FOUND", payload: res });
});
client.shop.fetchInfo().then(res => {
  store.dispatch({ type: "SHOP_FOUND", payload: res });
});

const App = ({ location }) => {
  const exclusionArray = ["/Login", "/Register", "/Waiver"];

  return (
    <Provider store={store}>
      <div className="App">
        <div>
          {exclusionArray.indexOf(location.pathname) < 0 && <Header />}
          <Route exact path="/" component={ImageText} />
          <Route exact path="/home" component={ImageText} />
          <Route exact path="/viewPrograms" component={Programs} />
          <Route exact path="/AboutUs" component={AboutUs} />
          <Route exact path="/ContactUs" component={ContactUs} />
          <Route exact path="/" component={Double00} />
          <Route exact path="/home" component={Double00} />
          <Route exact path="/" component={QParkour} />
          <Route exact path="/home" component={QParkour} />
          <Route exact path="/Login" component={Login} />
          <Route exact path="/Register" component={Register} />
          <Route exact path="/Waiver" component={Waiver} />
          <Route exact path="/displayBookings" component={DisplayBookings} />
          {/* <Route exact path="/addChildren/" component={AddChildren} /> */}

          <Route exact path="/Shop" component={Shop} />
          <Route
            exact
            path="/BookProgram/:activityName"
            component={BookProgramForm}
          />
          <Route exact path="/updateBooking/:id" component={UpdateBooking} />
          <Switch>
            <SecuredRoute exact path="/AdminPortal" component={AdminHome} />
            <SecuredRoute
              exact
              path="/AdminPortal/View/:activityName"
              component={AdminViewProgram}
            />
            <SecuredRoute
              exact
              path="/AdminPortal/ViewAllActivities"
              component={ViewAllAvtivities}
            />
            <SecuredRoute
              exact
              path="/AdminPortal/AdminViewBooking"
              component={AdminViewBooking}
            />
            <SecuredRoute
              exact
              path="/AdminPortal/addActivity"
              component={InsertActivityForm}
            />
            <SecuredRoute
              exact
              path="/AdminPortal/updateActivity"
              component={UpdateActivityForm}
            />
            <SecuredRoute
              exact
              path="/AdminPortal/deleteActivity"
              component={DeleteActivityPage}
            />
          </Switch>
        </div>
        {exclusionArray.indexOf(location.pathname) < 0 && <Footer />}
      </div>
    </Provider>
  );
};

export default withRouter(App);
