import React, { Component } from "react";
import { Link } from "react-router-dom";
import contactPrograms from "../../images/contact-us-programs.png";
import contactPhone from "../../images/contact-us-phone.png";

class ContactUs extends Component {
  render() {
    return (
      <div class="ui segment">
        <div class="ui text padding-contact-us-whole">
          <div class="ui link cards centered floated">
            <div class="card margin-contact-us-cards">
              <div class="image">
                <iFrame
                  src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d91585.17145545623!2d-77.362032!3d44.165105!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x1421534d1369ea23!2sThe+Station!5e0!3m2!1sen!2sca!4v1554233263895!5m2!1sen!2sca"
                  width="290"
                  height="285"
                  frameborder="0"
                  style={{ border: 0 }}
                  allowFullScreen
                ></iFrame>
              </div>

              <div class="content">
                <div class="header">Find Us On Google</div>
                <div class="description">
                  We'll help you out and show you exactly where we are located!
                </div>
              </div>
            </div>

            <div class="card margin-contact-us-cards">
              <div class="image">
                <img src={contactPrograms}></img>
              </div>
              <div class="content">
                <div class="header">Our Various Programs</div>
                <div class="description">
                  See a list of our offered programs with all their prices!
                </div>
                <Link to="/viewPrograms" class="ui red button right floated">
                  Learn More
                </Link>
              </div>
            </div>

            <div class="card margin-contact-us-cards">
              <div class="image">
                <img src={contactPhone}></img>
              </div>
              <div class="content">
                <div class="header">Need more Information?</div>

                <div class="description">
                  We're happy to give you any information still required! Give
                  us a call!
                </div>
                <label class="ui inverted red button right floated">
                  (123) 456-7890
                </label>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ContactUs;
