import React from "react";
import { Link } from "react-router-dom";

const BookNowButton = props => {
  const newTo = {
    pathname: `/BookProgram/${props.activity.activityName
      .split(" ")
      .join("-")}`,
    activityName: props.activity.activityName,
    activityIdentifier: props.activity.activityIdentifier,
    activityCurrentCapacity: props.activity.activityCurrentCapacity,
    activityMaxCapacity: props.activity.activityMaxCapacity,
    start_date: new Date(props.activity.start_date),
    end_date: new Date(props.activity.end_date)
  };

  return (
    <React.Fragment>
      <Link to={newTo} className="ui red basic button float">
        Book Now
      </Link>
    </React.Fragment>
  );
};

export default BookNowButton;
