import { SET_CURRENT_USER } from "../Actions/ActionType";

const initialState = {
  //if no one is logged in, set user to an empty object and token to false
  user: {},
  validToken: false
};

const boolActionPayload = payload => {
  if (payload) {
    return true;
  } else {
    return false;
  }
};

export default function(state = initialState, action) {
  switch (action.type) {
    case SET_CURRENT_USER:
      return {
        //object that contains state
        ...state,
        validToken: boolActionPayload(action.payload),
        user: action.payload
      };
    default:
      return state;
  }
}
