import axios from "axios";
import {
  GET_PROGRAMS,
  GET_BOOKINGS,
  GET_ERRORS,
  GET_BOOKING,
  DELETE_BOOKING,
  DELETE_PROGRAM,
  GET_MRBOOKING
} from "./ActionType";
import { async } from "q";

export const getPrograms = () => async dispatch => {
  const res = await axios.get(
    "https://thestation-booking.herokuapp.com/api/station/all"
  );
  dispatch({
    type: GET_PROGRAMS,
    payload: res.data //data we get from database (payload from projectReducer.js)
  });
};

export const getBookings = () => async dispatch => {
  const res = await axios.get(
    "https://thestation-booking.herokuapp.com/api/station/booking/allBookings"
  );
  dispatch({
    type: GET_BOOKINGS,
    payload: res.data
  });
};

export const getAdminBookings = () => async dispatch => {
  const res = await axios.get(
    "https://thestation-booking.herokuapp.com/api/station/booking/adminAllBookings"
  );
  dispatch({
    type: GET_BOOKINGS,
    payload: res.data
  });
};

export const createBooking = (booking, history) => async dispatch => {
  try {
    const res = await axios.post(
      "https://thestation-booking.herokuapp.com/api/station/booking",
      booking
    );
    history.push("/displayBookings");
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

