export const createActivity = (activity, history) => async dispatch => {
  try {
    const res = await axios.post(
      "https://thestation-booking.herokuapp.com/api/station/activities",
      activity
    );
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const updateActivity = (activity, id) => async dispatch => {
  try {
    const res = await axios.put(
      `https://thestation-booking.herokuapp.com/api/station/activities/${id}`,
      activity,
      id
    );
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const deleteActivity = id => async dispatch => {
  if (window.confirm("Are you sure you want to delete this entry?"))
    await axios.delete(
      `https://thestation-booking.herokuapp.com/api/station/activities/${id}`
    );
  dispatch({
    type: DELETE_PROGRAM,
    payload: id
  });
};

export const addChildren = (booking_id, child, history) => async dispatch => {
  await axios.post(
    `https://thestation-booking.herokuapp.com/api/station/booking/${booking_id}`,
    child
  );
  // if something goes wrong then send back to programs
  history.push(`/viewPrograms`);
};
