import React, { Component } from "react";

import ProgramsButton from "../../Programs/ProgramsButton";
import AboutUsButton from "../../AboutUs/AboutUsButton";
import LoginButton from "../../LoginRegister/LoginButton";
import PropTypes from "prop-types";
import ShopButton from "../../shopify/ShopButton";
import { connect } from "react-redux";
import { logout } from "../../../Actions/secActions";
import { Link } from "react-router-dom";
import ContactUsButton from "../../AboutUs/ContactUsButton";

class Header extends Component {
  logout() {
    this.props.logout();
    window.location.href = "/";
  }

  render() {
    const { validToken, user } = this.props.security;
    //change layout if user is logged in
    const userIsAuthenticated = (
      <React.Fragment>
        <div className="right item font">
          <Link to="/displayBookings" class="ui red button">
            <i className="user icon"></i>View Bookings
          </Link>
        </div>
        <div className="right item font">
          <Link
            to="/"
            className="ui red button"
            onClick={this.logout.bind(this)}
          >
            Logout
          </Link>
        </div>
      </React.Fragment>
    );

    //check if user is logged in AND contains admin privileges
    const userIsAuthenticatedAndAdmin = (
      <React.Fragment>
        <div className="right item font">
          <Link to="/AdminPortal" className="ui red button">
            Admin Portal
          </Link>
        </div>
        <div className="item font">
          <Link
            to="/"
            className="ui red button"
            onClick={this.logout.bind(this)}
          >
            Logout
          </Link>
        </div>
      </React.Fragment>
    );

    const userIsNotAuthenticated = <LoginButton />;

    let linksHeader;
    //logic for logged in users
    if (validToken && user) {
      console.log(user);
      if (user.isAdmin) {
        linksHeader = userIsAuthenticatedAndAdmin;
      } else {
        linksHeader = userIsAuthenticated;
      }
    } else {
      linksHeader = userIsNotAuthenticated;
    }

    return (
      <div className="ui inverted vertical masthead center aligned segment bgcolor">
        <div className="ui container">
          <div className="ui font-fam-all secondary menu spacing">
            <h3 className="tsb-font">
              The Station <br /> Belleville
            </h3>
            <Link to="/home" className="item font">
              Home
            </Link>
            <AboutUsButton />
            <ShopButton />
            <ProgramsButton />
            <ContactUsButton />

            {linksHeader}
          </div>
        </div>
        <div className="divider"></div>
      </div>
    );
  }
}

Header.propTypes = {
  logout: PropTypes.func.isRequired,
  security: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  security: state.security
});

export default connect(
  mapStateToProps,
  { logout }
)(Header);
