import React, { Component } from "react";
import Parkour from "../../images/parkour.jpg";
import Gymnastics from "../../images/gymnastics.jpg";
import Fitness from "../../images/fitness.jpg";
import Entertainment from "../../images/entertainment.jpg";
import Other from "../../images/other.jpg";
import BookNowButton from "../BookProgram/BookNowButton";
import BookNowFull from "../BookProgram/BookNowFull";
class ProgramItem extends Component {
  render() {
    const { program } = this.props;
    console.log(this.props);

    const activityFull = <BookNowFull />;

    const activityNotFull = <BookNowButton activity={program} />;

    let buttonToShow;
    let Image;

    if (program.activityCurrentCapacity == program.activityMaxCapacity) {
      buttonToShow = activityFull;
    } else {
      buttonToShow = activityNotFull;
    }

    if (program.activityType == "Parkour"){
      Image = Parkour
    }
    else if (program.activityType == "Gymnastics"){
      Image = Gymnastics
    }
    else if (program.activityType == "Fitness"){
      Image = Fitness
    }
    else if (program.activityType == "Entertainment"){
      Image = Entertainment
    }
    else{
      Image = Other
    }

    return (
      <div className="ui card area">
        <div className="image">
          <img src={Image} />
        </div>
        <div className="content">
          <div className="header">{program.activityName}</div>
          <div className="meta">{program.activityType}</div>
          <div className="meta"> Capacity: {program.activityCurrentCapacity} / {program.activityMaxCapacity}</div>
          <div className="meta">Start Date: {program.start_date}</div>
          <div className="meta">End Date: {program.end_date}</div>
        </div>
        <div className="extra content">{buttonToShow}</div>
      </div>
    );
  }
}

export default ProgramItem;
