import React, { Component } from "react";

import DatePicker from "react-datepicker";
import setHours from "date-fns/setHours";
import setMinutes from "date-fns/setMinutes";
import "react-datepicker/dist/react-datepicker.css";

import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createBooking, getBooking } from "../../Actions/projectActions";
import { format } from "date-fns";
import { Link } from "react-router-dom";
import AddChildren from "./AddChildren";

class BookProgramForm extends Component {
  state = {
    startDate: "",
    activityIdentifier: this.props.location.activityIdentifier,
    maxCapacity: this.props.location.activityMaxCapacity,
    currentCapacity: this.props.location.activityCurrentCapacity,
    activityStartDate: this.props.location.start_date,
    activityEndDate: this.props.location.end_date,
    errors: {},
    isOn: false,
    btnValue: "disabled"
  };

  handleChange = date => {
    this.setState({
      startDate: date
    });
  };

  handleCheck = e => {
    this.setState({ isOn: !this.state.isOn });
    if (this.state.isOn) {
      this.setState({
        btnValue: "disabled"
      });
    } else {
      this.setState({
        btnValue: ""
      });
    }
  };

  onSubmit(e) {
    e.preventDefault();
    const newBooking = {
      activityIdentifier: this.state.activityIdentifier,
      parentFirstName: this.state.parentFirstName,
      parentLastName: this.state.parentLastName,
      attendeeFirstName: this.state.attendeeFirstName,
      attendeeLastName: this.state.attendeeLastName,
      emailAddress: this.state.emailAddress,
      age: this.state.age,
      phoneNumber: this.state.phoneNumber,
      bookingDate: format(this.state.startDate, "yyyy-MMM-dd hh:mm:ss aa"),
      additionalInfo: this.state.additionalInfo,
      wasWaiverSigned: this.state.wasWaiverSigned,
      createdDate: format(new Date(), "yyyy-MMM-dd hh:mm:ss aa")
    };

    this.props.createBooking(newBooking, this.props.history);
  }

  //life cycle hooks
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  componentDidMount() {
    const { bookingId } = this.props.match.params;

    this.props.getBooking(bookingId, this.props.history);
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  render() {
    const { errors } = this.state;

    console.log("BOOKING FORM: " + this.props.location.activityName);
    console.log(errors);
    return (
      <React.Fragment>
        <h1 className="booking-header">{this.props.location.activityName}</h1>
        <div className="pusher-booking">
          <div className="ui raised very padded container segment">
            <form
              onSubmit={this.onSubmit.bind(this)}
              className="ui child-margin form "
              type="hidden"
            >
              <div className="fields">
                <div className="eight wide required field">
                  <label>Parent / Guardian's First Name</label>
                  <input
                    placeholder="First Name"
                    type="text"
                    required={true}
                    name="parentFirstName"
                    onChange={this.onChange.bind(this)}
                  />
                  <div style={{ fontSize: 12, color: "red" }}>
                    {" "}
                    {errors.parentFirstName}
                  </div>

                  <div className="ui error message">
                    {" "}
                    test {errors.parentFirstName}
                  </div>
                </div>
                <div className="eight wide required field">
                  <label>Last Name</label>
                  <input
                    placeholder="Last Name"
                    type="text"
                    required={true}
                    name="parentLastName"
                    onChange={this.onChange.bind(this)}
                  />
                  <div style={{ fontSize: 12, color: "red" }}>
                    {errors.parentLastName}
                  </div>
                </div>
              </div>

              <div className="fields">
                <div className="six wide required field">
                  <label>Phone</label>
                  <input
                    placeholder="Phone"
                    type="tel"
                    required={true}
                    name="phoneNumber"
                    onChange={this.onChange.bind(this)}
                  />
                  <div style={{ fontSize: 12, color: "red" }}>
                    {errors.phoneNumber}
                  </div>
                </div>
                <div className="six wide required field">
                  <label>Email</label>
                  <input
                    placeholder="Email"
                    type="email"
                    required={true}
                    name="emailAddress"
                    onChange={this.onChange.bind(this)}
                  />
                  <div style={{ fontSize: 12, color: "red" }}>
                    {errors.emailAddress}
                  </div>
                </div>
                <div className="six wide required field">
                  <label>Select a date </label>
                  <DatePicker
                    placeholderText="Click to pick Date"
                    onClickOutside
                    withPortal
                    isClearable
                    selected={this.state.startDate}
                    onChange={this.handleChange}
                    showTimeSelect
                    required={true}
                    minDate={this.state.activityStartDate}
                    maxDate={this.state.activityEndDate}
                    minTime={setHours(setMinutes(new Date(), 0), 10)}
                    maxTime={setHours(setMinutes(new Date(), 30), 18)}
                    dateFormat="yyyy-MMM-dd hh:mm:ss aa"
                    name="bookingDate"
                  />
                </div>
              </div>

              <div className="fields">
                <div className="six wide required field">
                  <label>Child's First Name</label>
                  <input
                    placeholder="First Name"
                    type="text"
                    required={true}
                    name="attendeeFirstName"
                    onChange={this.onChange.bind(this)}
                  />
                  <div style={{ fontSize: 12, color: "red" }}>
                    {errors.attendeeFirstName}
                  </div>
                </div>
                <div className="six wide required field">
                  <label>Last Name</label>
                  <input
                    placeholder="Last Name"
                    type="text"
                    required={true}
                    name="attendeeLastName"
                    onChange={this.onChange.bind(this)}
                  />
                  <div style={{ fontSize: 12, color: "red" }}>
                    {errors.attendeeLastName}
                  </div>
                </div>
                <div className="four wide field">
                  <label>Age</label>
                  <input
                    placeholder="Age"
                    type="number"
                    required={true}
                    name="age"
                    onChange={this.onChange.bind(this)}
                  />
                  <div style={{ fontSize: 12, color: "red" }}>{errors.age}</div>
                </div>
              </div>

              {/* This will be implemented in the future
              <AddChildren />
              */}

              <div className="fields">
                <div className="sixteen wide field">
                  <label>Additional info</label>
                  <textarea
                    rows="2"
                    placeholder="Medical conditions, allergies..."
                    name="additionalInfo"
                    onChange={this.onChange.bind(this)}
                  ></textarea>
                </div>
              </div>

              <div className="fields">
                <div className="sixteen wide field">
                  <div className="required inline field">
                    <div className="ui slider checkbox">
                      <input
                        type="checkbox"
                        name="wasWaiverSigned"
                        value="true"
                        checked={this.state.isOn}
                        onChange={this.handleCheck.bind(this)}
                      />
                      <label>
                        {" "}
                        I confirm that I have Read and Signed the Release Form{" "}
                      </label>
                    </div>
                  </div>
                </div>
              </div>

              <div className="fields">
                <div>
                  <button
                    className="ui button primary"
                    type="submit"
                    disabled={this.state.btnValue}
                    onSubmit={this.onSubmit.bind(this)}
                  >
                    Submit Form
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

BookProgramForm.propTypes = {
  createBooking: PropTypes.func.isRequired,
  getBooking: PropTypes.func.isRequired,
  booking: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  errors: state.errors,
  booking: state.booking.booking
});

export default connect(
  mapStateToProps,
  { createBooking, getBooking }
)(BookProgramForm);
