import React, { Component } from "react";
import { getPrograms } from "../../Actions/projectActions";
import AdminProgramItem from "./AdminProgramItem";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";

class ViewAllActivities extends Component {
  componentDidMount() {
    this.props.getPrograms();  
  }

 

  render() {
    const programs = this.props.program.programs;
    return (
      <div>
           <div className="admin-buttons">
                      <Link to="/AdminPortal/addActivity" className="ui blue submit button">
                        Insert New Activity
                      </Link>
              </div>
        <div className="programs-container">
          <div className="ui cards area">
            {programs.map(program => (
              <AdminProgramItem key={program.id} program={program} />
            ))}
          </div>
        </div>
      </div>
    );
  }
}

ViewAllActivities.propTypes = {
  program: PropTypes.object.isRequired,
  getPrograms: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  program: state.program
});

export default connect(
  mapStateToProps,
  { getPrograms }
)(ViewAllActivities);
