import axios from "axios";
import { GET_ERRORS, SET_CURRENT_USER } from "./ActionType";
import setJWToken from "../Utilities/setJWToken";
import jwt_decode from "jwt-decode";

export const createNewUser = (newUser, history) => async dispatch => {
  try {
    await axios.post(
      "https://thestation-booking.herokuapp.com/api/users/register",
      newUser
    );
    history.push("/Login");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const login = LoginRequest => async dispatch => {
  try {
    //hit endpoint with post request
    const res = await axios.post(
      "https://thestation-booking.herokuapp.com/api/users/login",
      LoginRequest
    );
    //extract token from res.data
    const { token } = res.data;
    //store the token in localStorage
    localStorage.setItem("jwtToken", token);
    //set token in the header
    setJWToken(token);
    //decode token on React
    const decoded = jwt_decode(token);
    //dispatch security reducer
    dispatch({
      type: SET_CURRENT_USER,
      payload: decoded
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const logout = () => dispatch => {
  localStorage.removeItem("jwtToken");
  //delete header containg JW Token
  setJWToken(false);

  dispatch({
    type: SET_CURRENT_USER,
    payload: {}
  });
};
