import React, { Component } from "react";
import { connect } from "react-redux";
import { createActivity } from "../../Actions/projectActions";
import { format } from "date-fns";
import DatePicker from "react-datepicker";
import PropTypes from "prop-types";

class InsertActivityForm extends Component {
  state = {
    activityName: this.props.location.activityName,
    activityType: this.props.location.activityType,
    activityDuration: this.props.location.activityDuration,
    activityMaxCapacity: this.props.location.activityMaxCapacity,
    activityCurrentCapacity: this.props.location.activityCurrentCapacity,
    start_date: this.props.location.start_date,
    end_date: this.props.location.end_date
  };

  onSubmit(e) {
    e.preventDefault();
    const newActivity = {
      activityName: this.state.activityName,
      activityType: this.state.activityType,
      activityDuration: this.state.activityDuration,
      activityMaxCapacity: this.state.activityMaxCapacity,
      activityCurrentCapacity: this.state.activityCurrentCapacity,
      start_date: format(this.state.start_date, "yyyy-MMM-dd"),
      end_date: format(this.state.end_date, "yyyy-MMM-dd")
    };
    this.props.createActivity(newActivity, this.props.history);
    this.props.history.push("/AdminPortal/ViewAllActivities");
  }

  handleChangeStart = date => {
    this.setState({
      start_date: new Date(date)
    });
  };

  handleChangeEnd = date => {
    this.setState({
      end_date: new Date(date)
    });
  };

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  render() {
    console.log(this.props.location.start_date);
    console.log(this.props.location.end_date);
    return (
      <React.Fragment>
        <div className="ui raised very padded container segment">
          <h1>Insert New Activity</h1>

          <form
            className="ui child-margin form"
            type="hidden"
            onSubmit={this.onSubmit.bind(this)}
          >
            <div className="eight wide required field">
              <label>Activity Name</label>
              <input
                placeholder="Activity Name"
                type="text"
                name="activityName"
                onChange={this.onChange.bind(this)}
                value={this.state.activityName}
                isRequired={true}
              />
            </div>

            <div className="eight wide required field">
              <label>Activity Type</label>
              <select
                name="activityType"
                class="ui fluid dropdown"
                onChange={this.onChange.bind(this)}
                isRequired={true}
              >
                <option value="">Select a Type</option>
                <option value="Parkour">Parkour</option>
                <option value="Gymnastics">Gymnastics</option>
                <option value="Fitness">Fitness</option>
                <option value="Entertainment">Entertainment</option>
                <option value="Other">Other</option>
              </select>
            </div>
            <div className="eight wide required field">
              <label>Activity Duration</label>
              <input
                placeholder="Activity Duration"
                type="text"
                name="activityDuration"
                onChange={this.onChange.bind(this)}
                isRequired={true}
              />
            </div>
            <div className="fields">
              <div className="four wide field">
                <label>Activity Current Capacity</label>
                <input
                  placeholder="Activity Current Capacity"
                  type="number"
                  name="activityCurrentCapacity"
                  onChange={this.onChange.bind(this)}
                  isRequired={true}
                />
              </div>
              <div className="four wide field">
                <label>Activity Max Capacity</label>
                <input
                  placeholder="Activity Max Capacity"
                  type="number"
                  name="activityMaxCapacity"
                  onChange={this.onChange.bind(this)}
                  isRequired={true}
                />
              </div>
            </div>
            <div className="eight wide required field">
              <label>Start Date</label>
              <DatePicker
                placeholderText="Click to pick Date"
                onClickOutside
                withPortal
                isClearable
                minDate={new Date()}
                selected={this.state.start_date}
                onChange={this.handleChangeStart}
                dateFormat="yyyy-MMM-dd"
                name="start_end"
                isRequired={true}
              />
            </div>

            <div className="eight wide required field">
              <label>End Date</label>
              <DatePicker
                placeholderText="Click to pick Date"
                onClickOutside
                withPortal
                isClearable
                minDate={new Date()}
                selected={this.state.end_date}
                onChange={this.handleChangeEnd}
                dateFormat="yyyy-MMM-dd"
                name="end_date"
                isRequired={true}
              />
            </div>
            <div className="fields">
              <div className="eight wide field centered">
                <button
                  type="submit"
                  onSubmit={this.onSubmit.bind(this)}
                  className="ui button large primary fluid center"
                >
                  <center>Add This Activity</center>
                </button>
              </div>
            </div>
          </form>
        </div>
      </React.Fragment>
    );
  }
}
InsertActivityForm.propTypes = {
  createActivity: PropTypes.func.isRequired,
  program: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  errors: state.errors,
  program: state.program
});

export default connect(mapStateToProps, { createActivity })(InsertActivityForm);
