import React, { Component } from "react";
import imageTest from "../../../images/parkourhomepage.png";
import LazyLoad from "react-lazy-load";
class QParkour extends Component {
  render() {
    return (
      <React.Fragment>
        <div id="parkourhome" class="ui program-bg vertical segment holder">
          <div class="ui container">
            <div class="ui stackable inverted divided equal height stackable grid">
              <div class="ten wide column">
                <h2 class="ui center aligned icon header">
                  <i class="circular users icon"></i>
                  Quinte Parkour
                </h2>
                <div class="ui divider"></div>
                <div class="change-font">
                  Our goal is to nurture bright, active futures through
                  curiosity and a free environment. We strive to better our
                  students through educational experiences both in and out of
                  the classroom. Quinte Parkour aims to create a haven where
                  students feel safe to tackle their fears and accomplish all of
                  their goals. We offer a variety of services designed to help
                  students be the best version of themselves, while having a fun
                  along the way.
                </div>
              </div>
              <div class="six wide column img-place">
                <img src={imageTest} className="animate-image" />
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default QParkour;
