import React from "react";
import { Link } from "react-router-dom";

const ProgramButton = () => {
  return (
    <React.Fragment>
      <div className="admin-buttons">
        <Link
          to="/AdminPortal/AdminViewBooking"
          className="ui blue submit button"
        >
          View Bookings
        </Link>
      </div>
    </React.Fragment>
  );
};

export default ProgramButton;
