import { GET_PROGRAMS, DELETE_PROGRAM } from "../Actions/ActionType";

const initialState = {
  programs: [],
  program: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_PROGRAMS:
      return {
        ...state,
        programs: action.payload
      };
      case DELETE_PROGRAM:
        return {
          ...state,
          programs: state.programs.filter(
            program => program.id !== action.payload
          )
        };
    default:
      return state;
  }
}
