import React, { Component } from "react";
import { Link } from "react-router-dom";

class LearnMoreButton extends Component {
  render() {
    return (
      <React.Fragment>
        <Link to="/AboutUs" className="ui  medium button red">
          Learn More <i className="learn-more-padd-bot right arrow icon"></i>
        </Link>
      </React.Fragment>
    );
  }
}

export default LearnMoreButton;
