import React, { Component } from "react";
import Products from "./Products";
import Cart from "./Cart";
import store from "../../store";
import { connect } from "react-redux";
import { myImage } from "../../../src/images/cart.png";

class Main extends Component {
  constructor() {
    super();
    this.updateQuantityInCart = this.updateQuantityInCart.bind(this);
    this.removeLineItemInCart = this.removeLineItemInCart.bind(this);
    this.handleCartClose = this.handleCartClose.bind(this);
    this.handleCartOpen = this.handleCartOpen.bind(this);
    this.addVariantToCart = this.addVariantToCart.bind(this);
  }

  addVariantToCart(variantId, quantity) {
    const state = store.getState(); // state from redux store
    const lineItemsToAdd = [{ variantId, quantity: parseInt(quantity, 10) }];
    const checkoutId = state.cart.checkout.id;
    state.cart.client.checkout
      .addLineItems(checkoutId, lineItemsToAdd)
      .then(res => {
        store.dispatch({
          type: "ADD_VARIANT_TO_CART",
          payload: { isCartOpen: true, checkout: res }
        });
      });
  }

  updateQuantityInCart(lineItemId, quantity) {
    const state = store.getState(); // state from redux store
    const checkoutId = state.cart.checkout.id;
    const lineItemsToUpdate = [
      { id: lineItemId, quantity: parseInt(quantity, 10) }
    ];
    state.cart.client.checkout
      .updateLineItems(checkoutId, lineItemsToUpdate)
      .then(res => {
        store.dispatch({
          type: "UPDATE_QUANTITY_IN_CART",
          payload: { checkout: res }
        });
      });
  }
  removeLineItemInCart(lineItemId) {
    const state = store.getState(); // state from redux store
    const checkoutId = state.cart.checkout.id;
    state.cart.client.checkout
      .removeLineItems(checkoutId, [lineItemId])
      .then(res => {
        store.dispatch({
          type: "REMOVE_LINE_ITEM_IN_CART",
          payload: { checkout: res }
        });
      });
  }
  handleCartClose() {
    store.dispatch({ type: "CLOSE_CART" });
  }
  handleCartOpen() {
    store.dispatch({ type: "OPEN_CART" });
  }

  render() {
    const state = store.getState();
    let oProducts = (
      <Products
        products={state.cart.products}
        client={state.cart.client}
        addVariantToCart={this.addVariantToCart}
      />
    );
    return (
      <div className="App">
        <h1 className="shopTitle">Shop The Station Belleville</h1>
        
       
        <div class="ui message">
        <div class="header">
          Changes in Service
        </div>
        <p> We were excited to show you all the merchandise we had but the Shop page had been integrated with Shopify API and our Shopify account has been expired.</p>
      </div>
        <div className="App__view-cart-wrapper">
          <button
            className="App__view-cart"
            id="cartButton"
            onClick={this.handleCartOpen}
          ></button>
        </div>

        {oProducts}
        <Cart
          checkout={state.cart.checkout}
          isCartOpen={state.cart.isCartOpen}
          handleCartClose={this.handleCartClose}
          updateQuantityInCart={this.updateQuantityInCart}
          removeLineItemInCart={this.removeLineItemInCart}
        />
      </div>
    );
  }
}

export default connect(state => state)(Main);
