import React, { Component } from "react";
import { connect } from "react-redux";
import ProgramButton from "./ProgramButton";
import ActivityButton from "./ActivityButton";
import { getMostRecentBooking } from "../../Actions/projectActions";
import PropTypes from "prop-types";
import employeeSchedImg from "../../images/calendar.png";
import activityImg from "../../images/AP-activity.png";
import bookingImg from "../../images/AP-book.png";

class AdminHome extends Component {
  componentDidMount() {
    this.props.getMostRecentBooking();
  }

  render() {
    let mrBooking = this.props.booking.mrbooking;
    console.log(mrBooking);

    return (
      <div>
        <h3 className="home-title">
          <span>Admin Portal</span>
        </h3>
        <div className="ui segment">
          <div className="ui placeholder segment">
            <div className="ui two column very relaxed stackable grid">
              <div className="column">
                <div className="wrapper-slide-left">
                  <div id="slide-left" className="view-booking-col ui form">
                    <div className="field">
                      <span id="style">
                        <img
                          src={bookingImg}
                          className="img-tw animate-image"
                        />
                      </span>
                    </div>
                    <ProgramButton />
                  </div>
                </div>
              </div>
              <div className="middle aligned column">
                <div className="wrapper-slide-right">
                  <div id="slide-right" className="view-activity-col ui form">
                    <div className="field">
                      <span id="style">
                        <img
                          src={activityImg}
                          className="img-tw animate-image"
                        />
                      </span>
                    </div>
                    <ActivityButton />
                  </div>
                </div>
              </div>
            </div>
            {/* <div class="ui vertical divider"></div> */}
          </div>
          <div class="ui inverted divider"></div>
          <div className="ui placeholder segment">
            <div className="ui two column very relaxed stackable grid">
              <div className="column">
                <div className="wrapper-slide-left">
                  <div id="slide-left" className="recent-bookings ui form">
                    <h2 id="recent-booking">Most Recent Booking</h2>
                    <div id="mrBooking">
                      <h1>{mrBooking.activityIdentifier}</h1>
                      <h3>
                        Attendee: {mrBooking.attendeeFirstName}{" "}
                        {mrBooking.attendeeLastName}
                      </h3>
                      <h3>Attending Date: {mrBooking.bookingDate}</h3>
                      <h3>
                        Parent: {mrBooking.parentFirstName}{" "}
                        {mrBooking.parentLastName}
                      </h3>
                      <h3>Phone: {mrBooking.phoneNumber}</h3>
                    </div>
                  </div>
                </div>
              </div>
              <div className="middle aligned column">
                <div className="wrapper-slide-right">
                  <div id="slide-right" className="empoyees-schedule ui form">
                    <div className="field">
                      <span id="style">
                        <img
                          src={employeeSchedImg}
                          className="img-tw animate-image"
                        />
                      </span>
                    </div>
                    <div className="admin-buttons">
                      <a href="#" className="ui blue submit button">
                        Employees schedule
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <h4 class="ui horizontal inverted divider"></h4>
        </div>
      </div>
    );
  }
}

AdminHome.propTypes = {
  booking: PropTypes.object.isRequired,
  getMostRecentBooking: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  booking: state.booking
});

export default connect(
  mapStateToProps,
  { getMostRecentBooking }
)(AdminHome);
