import React, { Component } from "react";

const emailRegex = RegExp(
  /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
);

const phoneRegex = RegExp(/^\(?([0-9]{3})\)?[-. ]?([0-9]{3})[-. ]?([0-9]{4})$/);

const formValid = ({ formErrors, ...rest }) => {
  let valid = true;

  // validate form errors being empty
  Object.values(formErrors).forEach(val => {
    val.length > 0 && (valid = false);
  });

  // validate the form was filled out
  Object.values(rest).forEach(val => {
    val === null && (valid = false);
  });

  return valid;
};

class Register extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isChecked: false,
      btnValue: "disabled",
      firstName: null,
      lastName: null,
      email: null,
      phone: null,
      formErrors: {
        firstName: "",
        lastName: "",
        email: "",
        phone: ""
      }
    };
    this.handleCheck = this.handleCheck.bind(this);
  }

  handleCheck = e => {
    this.setState({ isChecked: !this.state.isChecked });
    if (this.state.isChecked) {
      this.setState({
        btnValue: "disabled"
      });
    } else {
      this.setState({
        btnValue: ""
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();

    if (formValid(this.state)) {
      console.log(`
        --SUBMITTING--
        First Name: ${this.state.firstName}
        Last Name: ${this.state.lastName}
        Email: ${this.state.email}
        Phone: ${this.state.phone}
      `);
    } else {
      console.error("FORM INVALID - DISPLAY ERROR MESSAGE");
    }
  };

  handleChange = e => {
    e.preventDefault();
    const { name, value } = e.target;
    let formErrors = { ...this.state.formErrors };

    switch (name) {
      case "firstName":
        formErrors.firstName =
          value.length < 3 ? "minimum 3 characaters required" : "";
        break;
      case "lastName":
        formErrors.lastName =
          value.length < 2 ? "minimum 2 characaters required" : "";
        break;
      case "email":
        formErrors.email = emailRegex.test(value)
          ? ""
          : "invalid email address";
        break;
      case "phone":
        formErrors.phone = phoneRegex.test(value) ? "" : "Invalid phone number";
        break;
      default:
        break;
    }

    this.setState({ formErrors, [name]: value }, () => console.log(this.state));
  };

  render() {
    const { formErrors } = this.state;

    return (
      <React.Fragment>
        {/* <div className="modal" id="mymodal">
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h2 className="modal-title">Terms and Conditions</h2>
              </div>
              <div className="modal-body">
                <p>This is modal body</p>
              </div>
            </div>
          </div>
        </div> */}

        <div className="wrapper">
          <div className="form-wrapper">
            <h1 className="h1Style">Waiver Form</h1>
            <p>
              Please fill in the information below to complete your liability
              waiver.
            </p>
            <p>
              Please note that you must be at least 18 years old in order to
              sign a liability waiver. If you are not 18 years old, please ask
              your parent or legal guardian to complete this process.
            </p>

            <form
              className="formStyling"
              onSubmit={this.handleSubmit}
              noValidate
            >
              <div className="firstName">
                <label htmlFor="firstName">First Name</label>
                <input
                  className={formErrors.firstName.length > 0 ? "error" : null}
                  placeholder="First Name"
                  type="text"
                  name="firstName"
                  noValidate
                  onChange={this.handleChange}
                />
                {formErrors.firstName.length > 0 && (
                  <span className="errorMessage">{formErrors.firstName}</span>
                )}
              </div>
              <div className="lastName">
                <label htmlFor="lastName">Last Name</label>
                <input
                  className={formErrors.lastName.length > 0 ? "error" : null}
                  placeholder="Last Name"
                  type="text"
                  name="lastName"
                  noValidate
                  onChange={this.handleChange}
                />
                {formErrors.lastName.length > 0 && (
                  <span className="errorMessage">{formErrors.lastName}</span>
                )}
              </div>
              <div className="email">
                <label htmlFor="email">Email</label>
                <input
                  className={formErrors.email.length > 0 ? "error" : null}
                  placeholder="Email"
                  type="email"
                  name="email"
                  noValidate
                  onChange={this.handleChange}
                />
                {formErrors.email.length > 0 && (
                  <span className="errorMessage">{formErrors.email}</span>
                )}
              </div>
              <div className="phone">
                <label htmlFor="phone">Phone Number</label>
                <input
                  className={formErrors.phone.length > 0 ? "error" : null}
                  placeholder="Phone Number"
                  type="tel"
                  name="phone"
                  pattern="[0-9]{3}-[0-9]{2}-[0-9]{3}"
                  noValidate
                  onChange={this.handleChange}
                />
                {formErrors.phone.length > 0 && (
                  <span className="errorMessage">{formErrors.phone}</span>
                )}
              </div>
              <input onChange={this.handleCheck} type="checkbox" id="a"></input>
              <label className="styling" for="a">
                By checking this box, you agree to the{" "}
                <a href="#" data-toggle="modal" data-target="#mymodal">
                  term and conditions
                </a>{" "}
                of the waiver
              </label>

              <div className="createAccount">
                <button disabled={this.state.btnValue} type="submit">
                  Submit
                </button>
              </div>
            </form>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Register;
