import React, { Component } from "react";
import "./Register.css";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createNewUser } from "../../Actions/secActions";
import CloseButton from "./CloseButton";

const emailRegex = RegExp(
  /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
);

const formValid = ({ formErrors, ...rest }) => {
  let valid = true;

  // validate form errors being empty
  Object.values(formErrors).forEach(val => {
    val.length > 0 && (valid = false);
  });

  // validate the form was filled out
  Object.values(rest).forEach(val => {
    val === null && (valid = false);
  });

  return valid;
};

class Register extends Component {
  constructor(props) {
    super(props);

    this.state = {
      firstName: null,
      lastName: null,
      email: null,
      password: null,
      confirmPassword: null,
      formErrors: {
        firstName: "",
        lastName: "",
        email: "",
        password: "",
        confirmPassword: ""
      }
    };

    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  onSubmit = e => {
    e.preventDefault();
    const newUser = {
      firstName: this.state.firstName,
      lastName: this.state.lastName,
      email: this.state.email,
      password: this.state.password,
      confirmPassword: this.state.confirmPassword
    };

    this.props.createNewUser(newUser, this.props.history);
  };

  handleSubmit = e => {
    if (formValid(this.state)) {
      console.log(`
        --SUBMITTING--
        First Name: ${this.state.firstName}
        Last Name: ${this.state.lastName}
        Email: ${this.state.email}
        Password: ${this.state.password}
        Confirm Password : ${this.state.confirmPassword}
      `);
    } else {
      console.error("FORM INVALID - DISPLAY ERROR MESSAGE");
    }
  };

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  handleChange = e => {
    e.preventDefault();

    const { name, value } = e.target;
    let formErrors = { ...this.state.formErrors };

    switch (name) {
      case "firstName":
        formErrors.firstName =
          value.length < 3 ? "Min. 3 Characters Required" : "";
        break;
      case "lastName":
        formErrors.lastName =
          value.length < 2 ? "Min. 2 Characters Required" : "";
        break;
      case "email":
        formErrors.email = emailRegex.test(value)
          ? ""
          : "Please Enter Valid Email";
        break;
      case "password":
        formErrors.password =
          value.length < 6 ? "Min. 6 Characters Required" : "";
        break;
      case "confirmPassword":
        formErrors.confirmPassword =
          value != this.state.password
            ? "Confirm Password must match Password Box"
            : "";
        break;
      default:
        break;
    }

    this.setState({ formErrors, [name]: value }, () => console.log(this.state));
  };

  render() {
    const { formErrors } = this.state;

    return (
      <div className="wrapper">
        <div className="form-wrapper">
          <CloseButton />
          <form className="formStyling" onSubmit={this.onSubmit}>
            <div className="firstName">
              <label htmlFor="firstName"></label>
              <input
                id="fn"
                className={formErrors.firstName.length > 0 ? "error" : null}
                placeholder="First Name"
                type="text"
                required={true}
                name="firstName"
                value={this.state.firstName}
                noValidate
                onChange={this.handleChange}
                autoComplete="nope"
              />
              {formErrors.firstName.length > 0 && (
                <span className="login-error-control">
                  {formErrors.firstName}
                </span>
              )}
            </div>
            <div className="lastName">
              <label htmlFor="lastName"></label>
              <input
                id="ln"
                autoComplete="nope"
                className={formErrors.lastName.length > 0 ? "error" : null}
                placeholder="Last Name"
                type="text"
                required={true}
                name="lastName"
                value={this.state.lastName}
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.lastName.length > 0 && (
                <span className="login-error-control">
                  {formErrors.lastName}
                </span>
              )}
            </div>
            <br />
            <div className="email">
              <label htmlFor="email"></label>
              <input
                id="email"
                className={formErrors.email.length > 0 ? "error" : null}
                placeholder="Email"
                type="email"
                required={true}
                name="email"
                value={this.state.email}
                noValidate
                onChange={this.handleChange}
                autoComplete="off"
              />
              {formErrors.email.length > 0 && (
                <span className="login-error-control">{formErrors.email}</span>
              )}
            </div>
            <div className="password">
              <label htmlFor="password"></label>
              <input
                id="pass"
                className={formErrors.password.length > 0 ? "error" : null}
                placeholder="Password"
                type="password"
                required={true}
                name="password"
                value={this.state.password}
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.password.length > 0 && (
                <span className="login-error-control">
                  {formErrors.password}
                </span>
              )}
            </div>
            <div className="password">
              <label htmlFor="password"></label>
              <input
                id="cpass"
                className={
                  formErrors.confirmPassword.length > 0 ? "error" : null
                }
                placeholder="Confirm Password"
                type="password"
                required={true}
                name="confirmPassword"
                value={this.state.confirmPassword}
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.confirmPassword.length > 0 && (
                <span className="login-error-control">
                  {formErrors.confirmPassword}
                </span>
              )}
            </div>
            <div className="createAccount">
              <button
                className="submit-spce-reg ui fluid large red submit button"
                type="submit"
              >
                Create Account
              </button>
              <small>Already Have an Account?</small>
              <Link className="help-links-login" to="/Login">
                Login
              </Link>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

Register.propTypes = {
  createNewUser: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapeStateToprops = state => ({ errors: state.errors });

export default connect(
  mapeStateToprops,
  { createNewUser }
)(Register);
