import React, { Component } from "react";
import LearnMoreButton from "./LearnMoreButton";
class ImageText extends Component {
  render() {
    return (
      <div className=" text container float-left">
        <header>
          <h1 className="big-mainscreen-text">
            <span className="hero-text-color hero-text-1">Cultural.</span>
            <br />
            <span className="hero-text-color hero-text-2">Recreational.</span>
            <br />
            <span className="hero-text-color hero-text-3">Educational.</span>
            <br />
            <LearnMoreButton />
          </h1>
        </header>
      </div>
    );
  }
}

export default ImageText;
