import {
  GET_BOOKINGS,
  GET_BOOKING,
  DELETE_BOOKING,
  GET_MRBOOKING
} from "../Actions/ActionType";

const initialState = {
  bookings: [],
  booking: {},
  mrbooking: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_BOOKINGS:
      return {
        ...state,
        bookings: action.payload
      };
    case GET_MRBOOKING:
      return {
        ...state,
        mrbooking: action.payload
      };
    case GET_BOOKING:
      return {
        ...state,
        booking: action.payload
      };

    case DELETE_BOOKING:
      return {
        ...state,
        bookings: state.bookings.filter(
          booking => booking.id !== action.payload
        )
      };

    default:
      return state;
  }
}
