import React from "react";
import { Link } from "react-router-dom";


const ViewButton = props => {
  const newTo = {
    pathname: `/AdminPortal/View/${props.activity.activityName
      .split(" ")
      .join("-")}`,
      activityName: props.activity.activityName,
      activityType: props.activity.activityType,
      activityDuration: props.activity.activityDuration,
      activityCurrentCapacity: props.activity.activityCurrentCapacity,
      activityMaxCapacity: props.activity.activityMaxCapacity,
      start_date: new Date(props.activity.start_date),
      end_date: new Date(props.activity.end_date)
  };
  
  const UpdateTo = {
    pathname: `/AdminPortal/updateActivity`,
    id: props.activity.id,
    activityName: props.activity.activityName,
    activityType: props.activity.activityType,
    activityDuration: props.activity.activityDuration,
    activityCurrentCapacity: props.activity.activityCurrentCapacity,
    activityMaxCapacity: props.activity.activityMaxCapacity,
    start_date: new Date(props.activity.start_date),
    end_date: new Date(props.activity.end_date)
  };

  const DeleteTo = {
    pathname: `/AdminPortal/deleteActivity`,
    id: props.activity.id,
    activityName: props.activity.activityName,
    activityType: props.activity.activityType,
    activityDuration: props.activity.activityDuration,
    activityCurrentCapacity: props.activity.activityCurrentCapacity,
    activityMaxCapacity: props.activity.activityMaxCapacity,
    start_date: new Date(props.activity.start_date),
    end_date: new Date(props.activity.end_date)
  };
//format(this.state.end_date, "yyyy-MMM-dd hh:mm:ss aa")
  return (
    <React.Fragment>
      <Link to={UpdateTo} id="admin-update-prg" className="ui blue basic button">
        Update
      </Link>
      <Link
        to={newTo}
        id="admin-view-prg"
        className="ui green basic button float"
      >
        View
      </Link>
      
      <Link to={DeleteTo}  id="admin-delete-prg" className="ui red basic button">
        Delete
      </Link>

    </React.Fragment>
  );
};

export default ViewButton;
